.project {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: row;
        flex-direction: row;
    margin: 20px 12px;
    padding: 15px;
    -webkit-transition: 0.4s ease;
    -o-transition: 0.4s ease;
    transition: 0.4s ease;
}

.project:hover {
    /* border: 2px solid rgba(92, 155, 223, 0.3); */
    -webkit-box-shadow: 2px 3px 15px rgba(92, 155, 223, 0.7);
            box-shadow: 2px 3px 15px rgba(92, 155, 223, 0.7);
    border-radius: 20px;
    padding: 18px;
}

.project a {
    color: rgba(0, 0, 0, 0.5);
}

.project__img {
    -webkit-box-shadow: 5px 5px 6px 1px;
            box-shadow: 5px 5px 6px 1px;
    border-radius: 20px;
    width: 300px;
    height: 240px;
    -webkit-transition: 0.4s ease;
    -o-transition: 0.4s ease;
    transition: 0.4s ease;
}

.project__img:hover {
    color: rgba(0, 0, 0, 0.5);
    opacity: 0.85;
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}

.project__info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    margin-left: 30px;
}

.project__title {
    color: black;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
}

.project__desc {
    color: rgba(0, 0, 0, 0.7);
    font-size: 16px;
    margin-bottom: 10px;
    max-width: 500px;
}

.project__techs {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: left;
        justify-content: left;
    width: 75%;
    max-width: 300px;
}

.tech-icon {
    height: 40px;
    width: 40px;
    margin: auto;
}

.project__hr {
    display: none;
}


@media screen and (max-width: 800px) {
    .project {
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
        -ms-flex-align: center;
            align-items: center;
        margin: 12px;
    }

    .project__info {
        margin-bottom: 20px;
        max-width: 300px;
    }

    .project__title {
        text-align: center;
    }

    .project__techs {
        width: 100%;
    }

    .project__hr {
        background: rgba(92, 155, 223, 0.3);
        display: block;
        margin: 45px 0; 
        width: 100%;
    }
}



